.d-flex {
  display: flex;
}
.align-items-center {
  align-items: center;
}
.justify-content-center {
  justify-content: center;
}
.justify-content-space-between {
  justify-content: space-between;
}
.justify-content-end {
  justify-content: flex-end;
}
.space-x {
  margin-left: 4px;
  margin-right: 4px;
}

button:focus,
.btn,
button:hover {
  box-shadow: none !important;
  border-color: default;
}
